         Choosecategory(){
            for (let i = 0; i < catdata.length; i++) {
                 if(currentcat=== catdata[i][0].category){break;}
                 loopednum=(i)
            }
            currentcategory = catdata[loopednum + 1]
        this.props.functionCallFromParent(currentcategory);
    }

    
    
